
.myModal{
      position: fixed;
      top:0;
      bottom:0;
      right:0;
      left:0;
      display: none;
      background-color: rgba(0, 0, 0, 0.51);
      z-index: 1;
}
.myModalContent{
      padding: 25px;
      background: white;
      border-radius: 16px;
      min-width: 250px;
}
.myModal.active{
      display: flex;
      justify-content: center;
      align-items:flex-start;
      padding-top: 400px;

}